import { Button, Slide, Snackbar } from "@mui/material";
import { ReactElement, useState } from "react";

export const useSnackbar = () => {
    const [open, setOpen] = useState<ReactElement<any, any> | undefined>(undefined);

    const openSnackbar = (el: ReactElement<any, any>) => {
        setOpen(el);
    }

    const closeSnackbar = () => {
        setOpen(undefined);
    }

    const snack = (
        <Snackbar
            open={open ? true : false}
            TransitionComponent={props => <Slide {...props} direction="right" />}
        >
            {
                open
            }
        </Snackbar>
    );

    return {
        openSnackbar,
        closeSnackbar,
        snack
    };
}